import React from "react";
import { Text } from "../Text/Text";
import { Label } from "../Label/Label";
import { Input, Textarea, Select } from "../Input/Input";
import s from "./Form.module.css";
import { Icon } from "../Icon/Icon";
//
import Datetime from "react-datetime";
import "react-datetime/css/react-datetime.css";

const conditions = [{ id: 1, name: "fca", description: "FCA(Free Carrier)" }];
const truckType = [{ id: 1, name: "big", description: "90m3" }];

export const Form = () => {
  const onChange = (e) => {};

  return (
    <form className={s.form}>
      <Label label={"   "} star={true}>
        <div className={s.inputWithIcon}>
          <Icon
            name={"location"}
            color={"grey"}
            size={32}
            className={s.icon}
          ></Icon>
          <Input
            type={"text"}
            value={" (89.)"}
            name={"location"}
            onChange={onChange}
          ></Input>
        </div>
      </Label>
      <Label label={" "}>
        <div className={s.inputWithIcon}>
          <Icon
            name={"customs"}
            color={"grey"}
            size={32}
            className={s.icon}
          ></Icon>
          <Input
            type={"text"}
            value={"  "}
            name={"customs"}
            onChange={onChange}
          ></Input>
        </div>
      </Label>
      <Label label={"   "} star={true}>
        <div className={s.inputWithIcon}>
          <Icon
            name={"finish"}
            color={"grey"}
            size={32}
            className={s.icon}
          ></Icon>
          <Input
            type={"text"}
            value={"."}
            name={"finish"}
            onChange={onChange}
          ></Input>
        </div>
      </Label>
      <Label label={"  "}>
        <div className={s.inputWithIcon}>
          <Icon
            name={"calendar"}
            color={"grey"}
            size={32}
            className={s.icon}
          ></Icon>
          <div className={s.calendar}>
            <Datetime
              type={"date"}
              onChange={onChange}
              name={"calendar"}
              dateFormat="DD.MM.YYYY"
              timeFormat={false}
            ></Datetime>
          </div>
        </div>
      </Label>
      <Label label={"  "} star={true}>
        <div className={s.inputWithIcon}>
          <Icon
            name={"truck"}
            color={"grey"}
            size={32}
            className={s.icon}
          ></Icon>
          <Select list={truckType}></Select>
        </div>
        <Text>
          <span className={s.truckInfo}>
            :{" "}
            <span className={s.truckNumbers}>
               {truckType[0].description}
            </span>
            . :{" "}
            <span className={s.truckNumbers}> 22 </span>
          </span>
        </Text>
      </Label>
      <Label label={" "}>
        <Select list={conditions} style={s.conditions}></Select>
      </Label>
      <Label label={""} style={s.commentTitle}>
        <Textarea
          onChange={onChange}
          value={
            " : 22-23,  3 ,   ."
          }
        ></Textarea>
      </Label>
    </form>
  );
};
